import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
// import CardActions from '@material-ui/core/CardActions';
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
// import Button from '@material-ui/core/Button';
import Typography from "@material-ui/core/Typography";
import { useHistory } from "react-router-dom";

import { Star, StarHalf } from "@material-ui/icons";
import Icon from "@mdi/react";
import { mdiCoffee, mdiPill } from "@mdi/js";

import yellow from '@material-ui/core/colors/yellow';

// import Paper from "@material-ui/core/Paper";
// import Grid from "@material-ui/core/Grid";

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1
  },
  root2: {
    maxWidth: 345
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary
  }
}));

const reviews = [
  {
    title: "Caffeine",
    alt: "caffeine",
    path: "/reviews/drugs/caffeine",
    imgurl: "https://i.imgur.com/iu2AJTc.jpg",
    rating: 5,
    type: "beverage",
    icon: <Icon path={mdiCoffee} size={1} />,
    quote:
      "I began drinking coffee regularly when I was about 25. It's a relaxing beverage with the handy side-effect of making you feel more awake or alert. I'll drink a cup of coffee usually in the afternoon when at work, or in the morning on weekends. I hear there are those who partake of caffeine in pill form, but that doesn't sound as enjoyable as a cup of coffee to me. I usually buy pre-ground coffee, but from time to time I'll buy beans and grind them myself; it makes for a very pungent, fresh cup of joe. "
  },
  {
    title: "Aspirin",
    alt: "aspirin",
    path: "/reviews/film/aspirin",
    imgurl: "https://i.imgur.com/Xpdx0Hu.jpg",
    rating: 4,
    type: "pill",
    icon: <Icon path={mdiPill} size={1} />,
    quote:
      "I'm fortunate enough to seldom experience headaches, but when I do, aspirin is usually very effective at alleviating the pain. I had trouble swallowing pills as a child, but this hasn't been an issue in adulthood. "
  }
];

export default function DrugReviews() {
  const classes = useStyles();
  let history = useHistory();
  const stars = num => {
    let the_stars = [];
    let add_half_star = false;

    //account for half-stars
    if(num%1 !== 0){
      num = num-1;
      add_half_star = true;
    }

    if (num > 0) {
      for (let i = 0; i < num; i++) {
        the_stars.push(<Star />);
      }
    }
    if(add_half_star === true){
      the_stars.push(<StarHalf />)
    }
    return <div>{the_stars}</div>;
  };

  return (
    <div className={classes.root} style={{ display: "flex", flexWrap: "wrap" }}>
      {reviews.map(review => {
        return (
          <Card
            className={classes.root2}
            style={{ marginTop: "10px", marginBottom: "10px", marginLeft: "10px", marginRight: "10px" }}
            onClick={() => {
              history.push(review.path);
            }}
          >
            <CardActionArea>
              <CardMedia component="img" alt={review.alt} height="320" image={review.imgurl} title={review.title} />
              <CardContent>
                <Typography gutterBottom variant="h5" component="h2">
                  <div style={{ display: "flex", justifyContent: "space-between" }}>
                    {review.title} {review.icon}
                  </div>
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p" style={{marginBottom: "10px"}}>
                  "{review.quote}"
                </Typography>
                <Typography variant="body2" gutterBottom color="textSecondary" component="p">
                  <div style={{color: yellow[800]}}>{stars(review.rating)}</div>
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>
        );
      })}
    </div>
  );
}
