import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
// import CardActions from '@material-ui/core/CardActions';
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
// import Button from '@material-ui/core/Button';
import Typography from "@material-ui/core/Typography";
import { useHistory } from "react-router-dom";

import { Star, StarHalf, VideogameAsset } from "@material-ui/icons";

import yellow from '@material-ui/core/colors/yellow';

// import Paper from "@material-ui/core/Paper";
// import Grid from "@material-ui/core/Grid";

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1
  },
  root2: {
    maxWidth: 345
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary
  }
}));

const reviews = [
  {
    title: "The Legend of Zelda: Majora's Mask",
    year: "2000",
    alt: "majorasmask",
    path: "/reviews/games/majorasmask",
    imgurl: "https://i.imgur.com/OAnWvVL.jpg",
    rating: 5,
    type: "game",
    icon: <VideogameAsset />,
    quote:
      "This darker take on the classic Legend of Zelda format has a rich open world with engaging characters and questlines that make your heroic efforts worth it. "
  },
  {
    title: "Overwatch",
    year: "2016",
    alt: "overwatch",
    path: "/reviews/games/overwatch",
    imgurl: "https://i.imgur.com/NTH2xpA.jpg",
    rating: 5,
    type: "game",
    icon: <VideogameAsset />,
    quote: "Overwatch takes a standard formula for team-based, objective-driven multiplayer and delivers an ultra-polished, creative result with a rich setting and fun mechanics that keeps you coming back for more. "
  },
  {
    title: "Amnesia: The Dark Descent",
    year: "2010",
    alt: "amnesia",
    path: "/reviews/games/amnesia",
    imgurl: "https://i.imgur.com/yIYqBCQ.jpg",
    rating: 5,
    type: "game",
    icon: <VideogameAsset />,
    quote: "Calling Amnesia a \"scary\" game doesn't quite do it justice. This survival-horror game sets a grim tone from the outset and requires the player to persevere through unnatural events, oppressive monsters, and grotesque puzzles as they try to lift a curse that has been placed upon them. "
  }
];

export default function GameReviews() {
  const classes = useStyles();
  let history = useHistory();

  const stars = num => {
    let the_stars = [];
    let add_half_star = false;

    //account for half-stars
    if(num%1 !== 0){
      num = num-1;
      add_half_star = true;
    }

    if (num > 0) {
      for (let i = 0; i < num; i++) {
        the_stars.push(<Star />);
      }
    }
    if(add_half_star === true){
      the_stars.push(<StarHalf />)
    }
    return <div>{the_stars}</div>;
  };

  return (
    <div className={classes.root} style={{ display: "flex", flexWrap: "wrap" }}>
      {reviews.map(review => {
        return (
          <Card
            className={classes.root2}
            style={{ marginTop: "10px", marginBottom: "10px", marginLeft: "10px", marginRight: "10px" }}
            onClick={() => {
              history.push(review.path);
            }}
          >
            <CardActionArea>
              <CardMedia component="img" alt={review.alt} height="480" image={review.imgurl} title={review.title} />
              <CardContent>
                <Typography variant="h5" component="h2">
                  <div style={{ display: "flex", justifyContent: "space-between" }}>
                    {review.title} {review.icon}
                  </div>
                </Typography>
                <Typography gutterBottom variant="subtitle2" component="h4">
                  {review.year}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p" style={{marginBottom: "10px"}}>
                  "{review.quote}"
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                  <div style={{color: yellow[800]}}>{stars(review.rating)}</div>
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>
        );
      })}
    </div>
  );
}
