import React from "react";
import { makeStyles } from "@material-ui/core/styles";

import { useHistory } from "react-router-dom";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";

import { Movie, Album, VideogameAsset, Tv, MenuBook } from "@material-ui/icons";
import Icon from "@mdi/react";
import { mdiPill, mdiPasta } from "@mdi/js";

const useStyles = makeStyles(theme => ({
  root: {
    maxWidth: 345
  }
}));

const review_categories = [
  {
    name: "Film",
    path: "/reviews/film",
    icon: <Movie style={{fontSize: '128px'}} />
  },
  {
    name: "Games",
    path: "/reviews/games",
    icon: <VideogameAsset style={{fontSize: '128px'}}  />
  },
  {
    name: "Music",
    path: "/reviews/music",
    icon: <Album style={{fontSize: '128px'}}  />
  },
  {
    name: "TV",
    path: "/reviews/tv",
    icon: <Tv style={{fontSize: '128px'}}  />
  },
  {
    name: "Books",
    path: "/reviews/books",
    icon: <MenuBook style={{fontSize: '128px'}}  />
  },
  {
    name: "Drugs",
    path: "/reviews/drugs",
    icon: <Icon path={mdiPill} size={"128px"} />
  },
  {
    name: "Food",
    path: "/reviews/food",
    icon: <Icon path={mdiPasta} size={"128px"} />
  },
];

export default function Reviews() {
  let history = useHistory();
  const classes = useStyles();

  return (
    <div style={{display: "flex", flexWrap: "wrap"}}>
      {review_categories.map(category => {
        return (
          <Card
          className={classes.root}
          onClick={() => {
            history.push(category.path);
          }}
          style={{ cursor: "pointer", textAlign: "center", minWidth: "15vw", marginLeft: "10px", marginRight: "10px", marginTop: "10px", marginBottom: "10px" }}
        >
          <CardContent>
            <div>
              {category.icon}
            </div>
            <h1>{category.name}</h1>
          </CardContent>
        </Card>
        );
      })}
    </div>
  );
}
