import React from "react";
import { createMuiTheme, withStyles, makeStyles, ThemeProvider } from "@material-ui/core/styles";

import Typography from "@material-ui/core/Typography";
import { useHistory } from "react-router-dom";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import IconButton from "@material-ui/core/IconButton";
import { green, red } from "@material-ui/core/colors";

import Icon from "@mdi/react";
import { mdiDiceD20, mdiDiceD6, mdiDiceD4, mdiDiceD8, mdiDiceD10, mdiDiceD12, mdiDiceMultiple, mdiCircleHalfFull, mdiPlusBox, mdiMinusBox, mdiReload } from "@mdi/js";
import { useState } from "react";

const useStyles = makeStyles(theme => ({
  button: {
    margin: theme.spacing(1)
  }
}));

// const theme = createMuiTheme({
//     palette: {
//       primary: green,
//     },
//   });

export default function DiceRoller() {
  //   let history = useHistory();
  const classes = useStyles();
  const [numCoins, setNumCoins] = useState(0); //default 0
  const [numD4s, setNumD4s] = useState(0); //default 0
  const [numD6s, setNumD6s] = useState(0); //default 0
  const [numD8s, setNumD8s] = useState(0); //default 0
  const [numD10s, setNumD10s] = useState(0); //default 0
  const [numD12s, setNumD12s] = useState(0); //default 0
  const [numD20s, setNumD20s] = useState(0); //default 0
  const [numD100s, setNumD100s] = useState(0); //default 0
  const [results, setResults] = useState({
    coins: [],
    d4s: [],
    d6s: [],
    d8s: [],
    d10s: [],
    d12s: [],
    d20s: [],
    d100s: []
  });

  const reset = () => {
    setNumCoins(0);
    setNumD4s(0);
    setNumD6s(0);
    setNumD8s(0);
    setNumD10s(0);
    setNumD12s(0);
    setNumD20s(0);
    setNumD100s(0);
    setResults({
      coins: [],
      d4s: [],
      d6s: [],
      d8s: [],
      d10s: [],
      d12s: [],
      d20s: [],
      d100s: []
    })
  }

  const rollDice = () => {
    //for each roll type, iterate over the number of that die type, roll it, and add it to the array of results for that die type

    let coin_results = [];
    for (let i = 0; i < numCoins; i++) {
      coin_results.push(Math.floor(Math.random() * 2) == 0 ? "Heads" : "Tails");
    }

    let d4_results = [];
    for (let i = 0; i < numD4s; i++) {
      d4_results.push(getRandomInt(1, 5));
    }

    let d6_results = [];
    for (let i = 0; i < numD6s; i++) {
      d6_results.push(getRandomInt(1, 7));
    }

    let d8_results = [];
    for (let i = 0; i < numD8s; i++) {
      d8_results.push(getRandomInt(1, 9));
    }

    let d10_results = [];
    for (let i = 0; i < numD10s; i++) {
      d10_results.push(getRandomInt(1, 11));
    }

    let d12_results = [];
    for (let i = 0; i < numD12s; i++) {
      d12_results.push(getRandomInt(1, 13));
    }

    let d20_results = [];
    for (let i = 0; i < numD20s; i++) {
      d20_results.push(getRandomInt(1, 21));
    }

    let d100_results = [];
    for (let i = 0; i < numD100s; i++) {
      d100_results.push(getRandomInt(1, 101));
    }

    let results = {
      coins: coin_results,
      d4s: d4_results,
      d6s: d6_results,
      d8s: d8_results,
      d10s: d10_results,
      d12s: d12_results,
      d20s: d20_results,
      d100s: d100_results
    };
    setResults(results); //update State
  };

  const getRandomInt = (min, max) => {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min)) + min; //The maximum is exclusive and the minimum is inclusive
  };

  const dice = [
    {
      name: "Coin Flip",
      icon: <Icon path={mdiCircleHalfFull} size={"48px"} />,
      addFunction: () => {
        console.log("Adding a Coin Flip...");
        setNumCoins(numCoins + 1);
      },
      removeFunction: () => {
        console.log("Removing a Coin Flip...");
        setNumCoins(numCoins - 1);
      },
      count: numCoins
    },
    {
      name: "D4",
      icon: <Icon path={mdiDiceD4} size={"48px"} />,
      addFunction: () => {
        setNumD4s(numD4s + 1);
      },
      removeFunction: () => {
        setNumD4s(numD4s - 1);
      },
      count: numD4s
    },
    {
      name: "D6",
      icon: <Icon path={mdiDiceD6} size={"48px"} />,
      addFunction: () => {
        setNumD6s(numD6s + 1);
      },
      removeFunction: () => {
        setNumD6s(numD6s - 1);
      },
      count: numD6s
    },
    {
      name: "D8",
      icon: <Icon path={mdiDiceD8} size={"48px"} />,
      addFunction: () => {
        setNumD8s(numD8s + 1);
      },
      removeFunction: () => {
        setNumD8s(numD8s - 1);
      },
      count: numD8s
    },
    {
      name: "D10",
      icon: <Icon path={mdiDiceD10} size={"48px"} />,
      addFunction: () => {
        setNumD10s(numD10s + 1);
      },
      removeFunction: () => {
        setNumD10s(numD10s - 1);
      },
      count: numD10s
    },
    {
      name: "D12",
      icon: <Icon path={mdiDiceD12} size={"48px"} />,
      addFunction: () => {
        setNumD12s(numD12s + 1);
      },
      removeFunction: () => {
        setNumD12s(numD12s - 1);
      },
      count: numD12s
    },
    {
      name: "D20",
      icon: <Icon path={mdiDiceD20} size={"48px"} />,
      addFunction: () => {
        setNumD20s(numD20s + 1);
      },
      removeFunction: () => {
        setNumD20s(numD20s - 1);
      },
      count: numD20s
    },
    {
      name: "D100",
      icon: (
        <span>
          <Icon path={mdiDiceD10} size={"48px"} />
          <Icon path={mdiDiceD10} size={"48px"} />
        </span>
      ),
      addFunction: () => {
        setNumD100s(numD100s + 1);
      },
      removeFunction: () => {
        setNumD100s(numD100s - 1);
      },
      count: numD100s
    }
  ];

  return (
    <Grid container>
      <Grid item style={{ display: "flex", flexDirection: "column", flexWrap: "wrap", flexBasis: "content" }} xs={2}>
        {dice.map(die => {
          return (
            <Card
              style={{
                //maxWidth: "40%",
                // cursor: "pointer",
                textAlign: "center",
                //minWidth: "10vw",
                marginLeft: "10px",
                marginRight: "10px",
                marginTop: "10px",
                marginBottom: "10px",
              }}
            >
              {/* <div> */}
                <div style={{fontWeight: "bold"}}>{die.name}</div>
                <div style={{display: "flex", flexDirection: "row", justifyContent: "space-evenly", flexWrap: "wrap", alignItems: "center", flexBasis: "content"}}>
                  <IconButton onClick={die.removeFunction} disabled={die.count === 0}>
                    <Icon path={mdiMinusBox} size={"32px"} color={red[500]} />
                  </IconButton>
                  {die.icon}
                  <IconButton onClick={die.addFunction}>
                    <Icon path={mdiPlusBox} size={"32px"} color={green[500]} />
                  </IconButton>
                </div>
                {/* </div> */}
            </Card>
          );
        })}
      </Grid>
      <Grid item xs={10} style={{ marginTop: "10px", marginBottom: "10px", paddingLeft: "20px" }}>
        <Card style={{ marginTop: "0px", marginBottom: "20px"}}>
          <CardContent>
            <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between" }}>
              <h1>Rolling: </h1>
              <Button
                variant="contained"
                color="primary"
                size="large"
                className={classes.button}
                startIcon={<Icon path={mdiDiceMultiple} size={"24px"} color="#ffffff" />}
                onClick={rollDice}
              >
                Roll!
              </Button>
            </div>
            <div>
              {dice.map(die => {
                if (die.count > 0) {
                  return (
                    <Typography variant="h5" color="textPrimary">
                      {die.name} x{die.count}
                    </Typography>
                  );
                } else {
                  return <span />;
                }
              })}
            </div>
          </CardContent>
        </Card>
        <Card style={{ marginTop: "20px", marginBottom: "20px", minHeight: "400px" }}>
          <CardContent>
            <div>
              <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between" }}>
              <h1>Results: </h1>
              <Button
                variant="contained"
                color="secondary"
                size="large"
                className={classes.button}
                startIcon={<Icon path={mdiReload} size={"24px"} color="#ffffff" />}
                onClick={reset}
              >
                Reset!
              </Button>
              </div>
              <div>
                {results.coins.length > 0 ? (
                  <Typography variant="h5" color="textPrimary">
                    <span style={{fontWeight: "bold"}}>Coin Flip{results.coins.length > 1 ? "s" : ""}:</span> {results.coins.join(", ")}
                  </Typography>
                ) : (
                  <span />
                )}
                {results.d4s.length > 0 ? (
                  <Typography variant="h5" color="textPrimary">
                    <span style={{fontWeight: "bold"}}>D4{results.d4s.length > 1 ? "s" : ""}:</span> {results.d4s.join(", ")}
                  </Typography>
                ) : (
                  <span />
                )}
                {results.d6s.length > 0 ? (
                  <Typography variant="h5" color="textPrimary">
                    <span style={{fontWeight: "bold"}}>D6{results.d6s.length > 1 ? "s" : ""}:</span> {results.d6s.join(", ")}
                  </Typography>
                ) : (
                  <span />
                )}
                {results.d8s.length > 0 ? (
                  <Typography variant="h5" color="textPrimary">
                    <span style={{fontWeight: "bold"}}>D8{results.d8s.length > 1 ? "s" : ""}:</span> {results.d8s.join(", ")}
                  </Typography>
                ) : (
                  <span />
                )}
                {results.d10s.length > 0 ? (
                  <Typography variant="h5" color="textPrimary">
                    <span style={{fontWeight: "bold"}}>D10{results.d10s.length > 1 ? "s" : ""}:</span> {results.d10s.join(", ")}
                  </Typography>
                ) : (
                  <span />
                )}
                {results.d12s.length > 0 ? (
                  <Typography variant="h5" color="textPrimary">
                    <span style={{fontWeight: "bold"}}>D12{results.d12s.length > 1 ? "s" : ""}:</span> {results.d12s.join(", ")}
                  </Typography>
                ) : (
                  <span />
                )}
                {results.d20s.length > 0 ? (
                  <Typography variant="h5" color="textPrimary">
                    <span style={{fontWeight: "bold"}}>D20{results.d20s.length > 1 ? "s" : ""}:</span> {results.d20s.join(", ")}
                  </Typography>
                ) : (
                  <span />
                )}
                {results.d100s.length > 0 ? (
                  <Typography variant="h5" color="textPrimary">
                    <span style={{fontWeight: "bold"}}>D100{results.d100s.length > 1 ? "s" : ""}:</span> {results.d100s.join(", ")}
                  </Typography>
                ) : (
                  <span />
                )}
              </div>
            </div>
          </CardContent>
        </Card>
      </Grid>
    </Grid>
  );
}
