import React from "react";
import { makeStyles } from "@material-ui/core/styles";

import { useHistory } from "react-router-dom";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";

import Icon from "@mdi/react";
import { mdiDiceD20 } from "@mdi/js";

const useStyles = makeStyles(theme => ({
  root: {
    maxWidth: 345
  }
}));

const tools = [
  {
    name: "Dice Roller",
    path: "/tools/dice",
    icon: <Icon path={mdiDiceD20} size={"128px"} />
  },
];

export default function Tools() {
  let history = useHistory();
  const classes = useStyles();

  return (
    <div style={{display: "flex", flexWrap: "wrap"}}>
      {tools.map(tool => {
        return (
          <Card
          className={classes.root}
          onClick={() => {
            history.push(tool.path);
          }}
          style={{ cursor: "pointer", textAlign: "center", minWidth: "15vw", marginLeft: "10px", marginRight: "10px", marginTop: "10px", marginBottom: "10px" }}
        >
          <CardContent>
            <div>
              {tool.icon}
            </div>
            <h1>{tool.name}</h1>
          </CardContent>
        </Card>
        );
      })}
    </div>
  );
}
