import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from "@material-ui/core/Typography";

function Home() {
  const posts = [
    {date: "March 19, 2020", title: "New Music and Game Reviews", text: <div><p>Similar to yesterday's update, there are now some reviews for music and games.</p></div>},
    {date: "March 18, 2020", title: "New Film Reviews", text: <div><p>I've added short reviews for some of my favorite films to the Reviews section. Ideally I'll add full, more comprehensive reviews for each of these films in the future. For now, it's mostly just little one-paragraph blurbs. </p></div>},
    {date: "March 17, 2020", title: "Cool New Status Updates", text: <div><p>Adding status updates section for funsies. There's not enough interesting stuff to put here so we're really reaching for content.</p></div>},
    {date: "March 11, 2020", title: "Website is Online", text: <div><p>The website is now online. We're expanding various sections here and there to add some variety.</p></div>}
  ];
  return (
      <div>
        {posts.map(post => {
          return (
            <Card variant="outlined" style={{marginTop: "10px", marginBottom: "10px"}}>
              <CardContent>
              <Typography variant="h4" component="h4">{post.title}</Typography>
                <Typography variant="subtitle1">{post.date}</Typography>
                <Typography variant="body1">{post.text}</Typography>
              </CardContent>
            </Card>
          );
        })}
      </div>
  );
}

export default Home;
