export default function TVReviews() {
  const classes = useStyles();
  let history = useHistory();
  const stars = num => {
    let the_stars = [];
    let add_half_star = false;

    //account for half-stars
    if (num % 1 !== 0) {
      num = num - 1;
      add_half_star = true;
    }

    if (num > 0) {
      for (let i = 0; i < num; i++) {
        the_stars.push(<Star />);
      }
    }
    if (add_half_star === true) {
      the_stars.push(<StarHalf />);
    }
    return <div>{the_stars}</div>;
  };

  return (
    <div className={classes.root} style={{ display: "flex", flexWrap: "wrap" }}>
      {reviews.map(review => {
        return (
          <Card
            className={classes.root2}
            style={{ marginTop: "10px", marginBottom: "10px", marginLeft: "10px", marginRight: "10px" }}
            onClick={() => {
              history.push(review.path);
            }}
          >
            <CardActionArea>
              <CardMedia component="img" alt={review.alt} height="480" image={review.imgurl} title={review.title} />
              <CardContent>
                <Typography variant="h5" component="h2">
                  <div style={{ display: "flex", justifyContent: "space-between" }}>
                    {review.title} {review.icon}
                  </div>
                </Typography>
                <Typography gutterBottom variant="subtitle2" component="h4">
                  {review.year}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p" style={{ marginBottom: "10px" }}>
                  "{review.quote}"
                </Typography>
                <Typography variant="body2" component="p">
                  <div style={{color: yellow[800]}}>{stars(review.rating)}</div>
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>
        );
      })}
    </div>
  );
}
