import React from "react";
import { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
// import CardActionArea from "@material-ui/core/CardActionArea";
// import CardActions from '@material-ui/core/CardActions';
import CardContent from "@material-ui/core/CardContent";
// import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Typography from "@material-ui/core/Typography";

import Grid from "@material-ui/core/Grid";

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1
  },
  root2: {
    maxWidth: 345
  }
}));

export default function Bulletin() {
  const classes = useStyles();
  const [posts, setPosts] = useState([]);
  const [postText, setPostText] = useState("");

  const submitPost = () => {
    let data = {
      text: postText
    };
    fetch("https://api.rudyharrelson.com/db/create/post", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(data) // body data type must match "Content-Type" header
    });
  };

  const fetchPosts = async () => {
    let response = await fetch("https://api.rudyharrelson.com/db/get/posts");
    response = await response.json();
    console.log("got posts");
    console.log(response);
    setPosts(response);
  };

  useEffect(() => {
    fetchPosts();
  }, []);

  return (
    <div className={classes.root} style={{ display: "flex", flexDirection: "column", justifyContent: "flex-start", alignItems: "center" }}>
      <div style={{display: "flex", flexDirection: "column", justifyContent: "flex-start", alignItems: "center", marginBottom: "20px"}}>
        <TextField
          variant="outlined"
          onChange={e => {
            console.log(e.target.value);
            setPostText(e.target.value);
          }}
          multiline
          rows="4"
          style={{marginBottom: "10px", width: "100%"}}
        />
        <br />
        <Button variant="contained" color="primary" size="large" onClick={submitPost}>
          Submit
        </Button>
      </div>
      <div>
        {posts.length > 0 ? (
          posts.map(post => {
            return (
              <Card className={classes.root2} key={post.post_id} style={{ marginTop: "10px", marginBottom: "10px" }}>
                <CardContent>
                  <Typography variant="body1" color="textPrimary" component="p" style={{ whiteSpace: "pre-line", marginBottom: "20px" }}>
                    {post.body}
                  </Typography>
                  <Typography variant="subtitle2" color="textSecondary" component="p">
                    #{post.post_id} @ ({post.date})
                  </Typography>
                </CardContent>
              </Card>
            );
          })
        ) : (
          <div>Loading...</div>
        )}
      </div>
    </div>
  );
}
